"use strict";

// window.console.log für den ausliefernden FE Server
var consoleHeadline = "font-size: 12px; font-weight: bold; color: #fff; background-color: #999;",
	consoleText = "color: #999";

jQuery(document).ready(function() {
	jQuery(".page-wrapper").contents().filter(function() {

		return this.nodeType === 8;
	}).each(function(i, e) {
		var test = e.nodeValue;
		if (test.toLocaleLowerCase().indexOf("seite generiert") !== -1) {
			var string = e.nodeValue.split(" - "),
				generiert = string[0].replace(" ", ""),
			server = string[1],
			wireframe = string[2],
			branch = string[3];
			window.console.log(" " + "\n" + "%c ------>>> FE Server Info <<<------ " + "\n" + "\n" + "%c" + server + "\n" + branch + "\n" + generiert + "\n" + wireframe + "\n" + " ", consoleHeadline, consoleText);
		}
	});
});