/* Implemenation of various Utility-functions.
	Author: Matthias Klebe
 */

"use strict";

var mobileThreshold = 768,
	utils = {
		isMobile: function() {
			return $(window).width() < mobileThreshold;
		},

		isDesktop: function() {
			return $(window).width() >= mobileThreshold;
		}
	};

if (jQuery.utils === undefined) {
	window.jQuery.utils = {};
}

window.jQuery.extend(jQuery.utils, utils);