"use strict";
/* globals grecaptcha */

$.fn.ajaxForm = function (options) {

	return this.each(function () {

		var that = this,
			$that = $(this),
			$form = $("form", this),
			$submitLink = $("a.submit", this),

			opts = $.extend({
				before: null,
				context: document.body,
				fail: null,
				done: null,
				type: "POST",
				url: "",
				reCaptcha: $form.find(".g-recaptcha").length
			}, options);

		if ($form.find(".g-recaptcha").length > 0) {
			$form.unbind("submit").bind("submit", function () {
				var data = $(this).serializeArray();

				if (typeof opts.before === "function") {
					data = opts.before.call(this, data);
				}


				$.ajax({
					"cache": false,
					"context": opts.context,
					"data": data,
					"dataType": "HTML",
					"type": opts.type,
					"url": opts.url
				}).done(function (data, textStatus, jqXHR) {
					console.log("ajax done");
					var tempDom = $("<div>").append($.parseHTML(data));
					if (!tempDom.find("input[name='success']").length) {
						$that.find(".form-result").html(data);
					}

					else {
						$that.html(data);
						$(window).scrollTop(parseInt($that.offset().top) - 200);
					}

					if (typeof opts.done === "function") {
						opts.done.call();
					}
				}).fail(function (jqXHR, textStatus, failThrown) {
					// log(jqXHR, textStatus, failThrown);
					if (typeof opts.fail === "function") {
						opts.fail.call();
					}
				});

				return false;
			});
		}
		else {

			$that.on("submit", "form", function () {
				var data = $(this).serializeArray();

				if (typeof opts.before === "function") {
					data = opts.before.call(this, data);
				}

				$.ajax({
					"cache": false,
					"context": opts.context,
					"data": data,
					"type": opts.type,
					"url": opts.url
				}).done(function (data, textStatus, jqXHR) {
					$that.html(data);
					if (typeof opts.done === "function") {
						opts.done.call();
					}
				}).fail(function (jqXHR, textStatus, failThrown) {
					// log(jqXHR, textStatus, failThrown);
					if (typeof opts.fail === "function") {
						opts.fail.call();
					}
				});

				return false;
			});
		}


		if (!opts.reCaptcha) {
			$submitLink.on("click", function () {
				$(this).closest("form").trigger("submit");
				return false;
			});

			$("input[type=submit]", this).on("click", function () {
				$("input[name=action]", that).val($(this).attr("name"));
			});
		}

		else {
			$form.trigger("submit");
		}
	});
};


$.fn.ajaxLink = function (options) {

	return this.each(function () {

		var that = this,

			opts = $.extend({
				before: null,
				context: document.body,
				data: null,
				fail: null,
				done: null,
				target: null,
				type: "POST",
				url: $(this).data("ajax-url") || $(this).attr("href")
			}, options);


		$(this)
			.click(function () {
				var data = opts.data;
				if (!data && (opts.type === "POST")) {
					data = "nocache=" + (Math.random() * 100000);
				}

				if (typeof opts.before === "function") {
					data = opts.before.call(this, data);
				}

				$.ajax({
					"cache": false,
					"context": opts.context,
					"data": data,
					"type": opts.type,
					"url": opts.url
				})
					.done(function (data, textStatus, jqXHR) {
						$(opts.target).html(data);
						if (typeof opts.done === "function") {
							opts.done.call();
						}
						// Wait for ad to load and center it afterwards
						window.setTimeout(function () {
							$(opts.target).trigger("center-ads");
						}, 500);
					})
					.fail(function (jqXHR, textStatus, failThrown) {
						// log(jqXHR, textStatus, failThrown);
						if (typeof opts.fail === "function") {
							opts.fail.call();
						}
					});

				return false;
			});
	});
};


/**
 * Lädt den Inhalt des angebundenen Elements von der im Parameter <code>url</code> übergebenen
 * AJAX-URL in periodischem Abstand immer wieder.
 *
 */
$.fn.ajaxRefresh = function (options) {
	return this.each(function () {

		var that = this,

			opts = $.extend({
				before: null,
				context: document.body,
				data: null,
				fail: null,
				initial: true,
				interval: null,
				refresh: true,
				done: null,
				type: "GET",
				url: null
			}, options);

		if (opts.initial) {
			$(that).ajaxUpdate(opts);
			opts.initial = false;
		}

		if (opts.refresh) {
			window.setTimeout(function () {
				$(that).ajaxUpdate(opts).ajaxRefresh(opts);
			}, opts.interval);
		}
	});
};


/**
 * Lädt den Inhalt des angebundenen Elements von der im Parameter <code>url</code> übergebenen
 * AJAX-URL.
 *
 * Beispiel: $("#elementId").ajaxUpdate({"url": "..."});
 */
$.fn.ajaxUpdate = function (options) {
	return this.each(function () {

		var that = this,

			opts = $.extend({
				before: null,
				context: document.body,
				data: null,
				fail: null,
				done: null,
				type: "GET",
				url: null
			}, options),

			data = opts.data;


		if (!data && (opts.type === "POST")) {
			data = "nocache=" + (Math.random() * 100000);
		}

		if (typeof opts.before === "function") {
			data = opts.before.call(this, data);
		}

		$.ajax({
			"cache": false,
			"context": opts.context,
			"data": data,
			"type": opts.type,
			"url": opts.url
		})
			.done(function (data) {
				$(that).html(data);
				if (typeof opts.done === "function") {
					opts.done.call();
				}
			})
			.fail(function () {
				if (typeof opts.fail === "function") {
					opts.fail.call();
				}
			});
	});
};


// Init --------------------------------------------------------------------------------------------

$.ajaxSetup({
	// Wichtig f. iOS 6:
	"cache": "false",
	"headers": { "cache-control": "no-cache" }
});