"use strict";

jQuery(document).ready(function () {

	function setAdBlockActive(enabled) {
		// window.console.log("About to set adblockerCookie to", enabled);

		// variable is used for additional JS-Script in google-tagmanager see:
		// publications/multi/src/main/webapp/template/widgets/tracking/view/helpers/google-tagmanager.jsp
		window.adBlockActive = enabled;
	}

	var test = document.createElement("div");
	test.innerHTML = "&nbsp;";
	test.className = "adsbox";
	document.body.appendChild(test);
	window.setTimeout(function() {
		if (test.offsetHeight === 0) {
			setAdBlockActive(true);
			// window.console.log("AdBlocker enabled");
		}
		else {
			setAdBlockActive(false);
			// window.console.log("NO AdBlocker");
		}
		test.remove();
	}, 100);
});

jQuery(window).on("load", function() {

	var adItems = jQuery(".ad").find("img").length,
		adIframes = jQuery(".ad").find("iframe").length,
		allAdItems = adItems + adIframes;

	// window.console.log("Anzahl der ausgespielten Werbemittel " + allAdItems);

	jQuery(".ad").each(function (index) {
		var adItem = jQuery(this).find("img").length,
			adIframe = jQuery(this).find("iframe").length;

		// window.console.log("Ad: ", index, adItem, adIframe);

		if (adItem > 0 || adIframe > 0) {
			// window.console.log("Adblocker not runnning");
			jQuery(this).find(".ad--marker-inner").removeAttr("style");
		}
		else {
			// window.console.log("Adblocker runnning");
			jQuery(this).css({
				margin: 0,
				height: 0
			});

			jQuery(this).find(".ad--marker-inner").css({
				height: 0,
				overflow: "hidden"
			});
		}
	});

	// window.console.log("adItems", adItems, "adIframes", adIframes);
	// window.console.log("adblockdetection loaded");

});
